*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --color-violet: #4f2ee8;
  --color-yellow: #eac645;
  --font-color-white: #fff;
  --font-color-grey: #6c6c6c;
  --font-color-violet: #4f2ee8;
  --font-color-yellow: #eac645;
  --font-color1: #111;
}

body.light {
  --background-color: #fff;
  --background-color1: #f6f6f6;
  --font-color: #111;
  --background-checkbox-theme: linear-gradient( 180deg, #4f2ee8 0%, #dcdcdc 100% );
  --border-color: #202024;
  --background-color-submit: #111;
  --text-color-submit: #f3f3f3;
  --text-buttons-submit: #11111180;
  --text-color-auth-form: #111;
  --background-color-auth-form: #f6f6f6;
  --background-color-submit-active: #333;
  --color-text-category: #1119;
  --color-text-categor-hover: #4f2ee8;
  --color-line-scroll: #e0e0e0;
  --background-color1: #f6f6f6;
  --font-color: #111;
  --background-checkbox-theme: linear-gradient( 180deg, #4f2ee8 0%, #dcdcdc 100% );
  --color-pagination-fill: #b4afaf;
}

body.dark {
  --background-color: #000;
  --background-color1: #202024;
  --font-color: #fff;
  --background-checkbox-theme: linear-gradient( 180deg, #4f2ee8 0%, #686868 100% );
  --border-color: #f6f6f6;
  --background-color-submit: #f6f6f6;
  --text-color-submit: #202024;
  --text-buttons-submit: #f6f6f680;
  --text-color-auth-form: #f6f6f6;
  --background-color-auth-form: #202024;
  --background-color-submit-active: #f6f6f6;
  --color-text-category: #fff9;
  --color-text-categor-hover: #eac645;
  --color-line-scroll: #1119;
  --background-color1: #202024;
  --font-color: #fff;
  --background-checkbox-theme: linear-gradient( 180deg, #4f2ee8 0%, #686868 100% );
  --color-pagination-fill: #fff;
}

@font-face {
  font-family: DM Sans Regular;
  src: url("DMSans-Regular.dac2326d.woff") format("woff"), url("DMSans-Regular.edc7ffdd.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans Italic;
  src: url("DMSans-Italic.e947e2a0.woff") format("woff"), url("DMSans-Italic.69cf9cd1.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans Medium;
  src: url("DMSans-Medium.ea7c3d0c.woff") format("woff"), url("DMSans-Medium.4c545a04.ttf") format("truetype");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans Bold;
  src: url("DMSans-Bold.c55f0cba.woff") format("woff"), url("DMSans-Bold.faef4562.ttf") format("truetype");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

body {
  background-color: var(--background-color1);
  color: var(--font-color);
  font-family: DM Sans Regular, sans-serif;
}

.container {
  width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 24px;
  }
}

.main-wrap {
  padding: 40px 0 80px;
}

@media screen and (min-width: 768px) {
  .main-wrap {
    padding: 40px 0 100px;
  }
}

@media screen and (min-width: 1440px) {
  .main-wrap {
    justify-content: center;
    gap: 40px;
    padding: 40px 0 72px;
    display: flex;
  }
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  width: 100%;
  position: relative;
}

.header-top {
  justify-content: space-between;
  display: flex;
}

.pos-fixed {
  width: 100%;
  position: fixed;
  top: 0;
  overflow: hidden;
}

.header-container {
  z-index: 1;
  background-color: var(--background-color);
  border: 1.5px solid var(--font-color);
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
  position: relative;
}

.logo-header {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-bookshelf {
  fill: var(--font-color);
}

.block-btn {
  justify-content: end;
  align-items: center;
  gap: 14px;
  display: flex;
}

.menu-btn {
  background-color: #0000;
  border: none;
  margin-left: auto;
  padding: 0;
  line-height: 0;
  display: inline-flex;
}

.menu-btn .open-menu, .menu-btn .close-menu {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.open-menu {
  stroke: var(--font-color);
}

.menu-btn .open-menu:hover, .menu-btn .open-menu:focus {
  stroke: var(--font-color-violet);
  transform: scale(.95);
}

.close-menu {
  stroke: var(--font-color);
}

.menu-btn .close-menu:hover, .menu-btn .close-menu:focus {
  stroke: var(--font-color-violet);
  transform: scale(.95);
}

.btn-singup, .header-nav {
  display: none;
}

.mobile-menu {
  z-index: 0;
  width: 100%;
  height: 100vh;
  background-image: radial-gradient(189.32% 190.93% at 59.76% -23.42%, var(--color-violet) 18.03%, #fff 100%);
  flex-direction: column;
  padding-top: 84px;
  transition: top .25s linear;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu.is-hidden {
  top: -100vh;
}

.mob-btn-singup {
  width: 133px;
  height: 37px;
  letter-spacing: -.02em;
  color: var(--font-color-white);
  background-color: #0000;
  border: #0000;
  align-items: center;
  gap: 9px;
  margin-bottom: 126px;
  padding: 0;
  font-family: DM Sans, sans-serif;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

.user-icon {
  fill: var(--font-color-grey);
}

.mob-header-nav {
  justify-content: center;
  align-items: center;
  margin-bottom: 126px;
  display: flex;
}

.mob-menu-container {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.current-color {
  background-color: var(--font-color-yellow);
  border-radius: 36px;
}

.home-link {
  letter-spacing: -.04em;
  color: var(--font-color);
  text-transform: uppercase;
  align-items: center;
  padding: 8px 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.3;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.home-link:hover, .home-link:focus {
  transform: scale(.95);
}

.shoping-link {
  letter-spacing: -.04em;
  color: var(--font-color-white);
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.3;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.shoping-link:hover, .shoping-link:focus {
  transform: scale(.95);
}

.bag-icon {
  fill: var(--font-color);
}

.btn-logout {
  width: 118px;
  height: 48px;
  border: 1.5px solid var(--font-color);
  background-color: var(--font-color-white);
  letter-spacing: -.04em;
  cursor: pointer;
  z-index: 999;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  padding: 14px 24px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
  position: absolute;
  bottom: 20px;
}

.arrow-right-icon {
  fill: var(--color-yellow);
}

.book-menu-img {
  width: 100%;
}

@media screen and (min-width: 0) and (max-width: 767px) {
  .header-nav {
    position: relative;
  }

  .mob-menu-container {
    position: absolute;
    top: 255px;
    left: 130px;
  }

  .mob-menu-container .bag-icon {
    fill: #fff;
  }

  .mob-menu-container .home-link {
    color: #111;
  }

  .shoping-link {
    margin-top: 14px;
  }

  .book-menu-img {
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

@media screen and (min-width: 768px) {
  .header-container {
    padding-top: 14px;
    padding-bottom: 13px;
  }

  .logo-header {
    margin-right: 40px;
  }

  .header-nav {
    margin-right: 87px;
    display: block;
  }

  .mobile-menu, .menu-btn {
    display: none;
  }

  .menu-container {
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .shoping-link.current-color, .home-link.current-color {
    color: var(--font-color1);
    font-weight: 700;
  }

  .shoping-link.current-color .bag-icon {
    fill: var(--font-color1);
  }

  .current-color {
    background-color: var(--font-color-yellow);
    border-radius: 36px;
  }

  .shoping-link {
    width: 156px;
    height: 34px;
    color: var(--font-color);
    padding: 7px 16px;
  }

  .block-btn {
    margin-right: 24px;
  }
}

@media screen and (min-width: 1440px) {
  .logo-header {
    margin-right: 76px;
  }

  .header-nav {
    margin-right: 0;
  }

  .menu-container {
    gap: 24px;
  }
}

@media screen and (min-width: 1440px) {
  .header-container {
    justify-content: space-between;
    display: flex;
  }
}

.check-text {
  align-items: center;
  gap: 5px;
  margin-bottom: 2px;
  display: flex;
  position: relative;
}

.check-text:hover {
  cursor: pointer;
}

.check-text:before {
  content: "";
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border: .1px solid #c3baeb;
  border-radius: 50px;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.check-text:after {
  content: "";
  width: 18px;
  height: 18px;
  background-color: #fff;
  border: .1px solid #c3baeb;
  border-radius: 50%;
  transition-duration: .25s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.check-theme:checked + .check-text:after {
  transform: translate(20px);
}

.dark-mode {
  color: #fff;
  background-color: #202024;
}

.dark-mode .check-text:before {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

.auth-logon {
  width: 184px;
  height: 48px;
  letter-spacing: -.56px;
  background-color: var(--color-violet);
  color: var(--font-color-white);
  border-color: #0000;
  border-radius: 18px;
  align-items: center;
  font-weight: 500;
  line-height: 1.28571;
  display: none;
}

.auth-logon-noneuser {
  width: 184px;
  justify-content: space-between;
  align-items: center;
  padding: 14px;
  display: flex;
}

.auth-logon-isuser {
  width: 184px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.auth-logon-isuser-text {
  color: var(--font-color-white);
  letter-spacing: -.44px;
  text-transform: capitalize;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.auth-logon .auth-logon-noneuser {
  padding: 14px;
}

.auth-logon .auth-logon-isuser {
  padding: 4px;
}

.auth-logout {
  width: 118px;
  height: 48px;
  border: 1.5px solid var(--border-color);
  background-color: var(--font-color-white);
  border-radius: 18px;
  align-items: center;
  padding: 15px 24px;
  display: flex;
  position: absolute;
}

.auth-logout-user {
  width: 118px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.auth-logout-user-text {
  color: #111;
  letter-spacing: -.56px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.28571;
}

.backdrop-auth {
  width: 100vw;
  height: 100vh;
  background-color: var(--color-violet);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-auth.is-hidden .modal-auth {
  transform: translate(-50%, -50%)scale(0);
}

.modal-auth {
  width: 335px;
  height: 384px;
  background-color: var(--background-color1);
  border: 2px solid var(--border-color);
  border-radius: 18px;
  padding: 40px 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal-button-auth-close {
  width: 20px;
  height: 20px;
  border: none;
  outline: none;
  padding: 0;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal-button-auth-close:focus .modal-button-auth-close-icon {
  stroke: var(--font-color-violet);
}

.modal-button-auth-close-icon {
  width: 20px;
  height: 20px;
  stroke: var(--font-color);
  background-color: var(--background-color1);
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-button-auth-close-icon:hover {
  stroke: var(--font-color-violet);
}

.modal-form-auth-field {
  flex-direction: column;
  display: flex;
}

.modal-form-auth-field-icon {
  width: 18px;
  height: 18px;
  stroke: var(--font-color);
  fill: var(--background-color1);
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
}

.modal-form-auth-field + .modal-form-auth-field {
  margin-top: 24px;
}

.modal-form-auth-field-input {
  width: 293px;
  height: 50px;
  border: 2px solid var(--border-color);
  color: var(--text-color-auth-form);
  background-color: var(--background-color-auth-form);
  letter-spacing: -.16px;
  text-transform: uppercase;
  border-radius: 40px;
  outline: #0000;
  align-items: center;
  gap: 10px;
  padding: 16px 50px 16px 24px;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.125;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.modal-form-auth-field-input::placeholder {
  color: var(--text-color-auth-form);
  letter-spacing: -.16px;
  text-transform: uppercase;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.125;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-form-auth-field-inputhover .modal-form-auth-field-icon {
  stroke: var(--font-color-violet);
}

.modal-form-auth-field-input:is(hover, focus)::placeholder {
  color: var(--font-color-violet);
}

.modal-form-auth-field-input:is(hover, focus) {
  border-color: var(--font-color-violet);
}

.modal-form-auth .pos-authfocus-within .modal-form-auth-field-icon {
  stroke: var(--font-color-violet);
}

.modal-form-auth .pos-auth {
  position: relative;
}

.sign-buttons-auth {
  justify-content: center;
  gap: 20px;
  margin-top: 14px;
  display: flex;
}

.sign-button-auth {
  color: var(--text-buttons-submit);
  background-color: var(--background-color-auth-form);
  letter-spacing: -.14px;
  text-transform: uppercase;
  border: none;
  margin-left: 0;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.28571;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.sign-button-auth:is(hover, focus) {
  color: var(--font-color-violet);
  outline: #0000;
  text-decoration-line: underline;
}

.sign-button-auth-submit {
  width: 294px;
  height: 50px;
  color: var(--text-color-submit);
  background-color: var(--background-color-submit);
  letter-spacing: -.16px;
  text-transform: uppercase;
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  padding: 14px 112px;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.125;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1), font-size .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.sign-button-auth-submit:is(hover, focus) {
  color: #aaa;
}

.sign-button-auth-submit:active {
  background-color: var(--background-color-submit-active);
  color: var(--text-color-submit);
  font-size: 15px;
  box-shadow: inset 0 0 26px 1px #000000bf;
}

@media screen and (min-width: 0) and (max-width: 767px) {
  .block {
    position: relative;
  }

  .block-auth {
    display: none;
    position: absolute;
    top: 32px;
    left: -346px;
  }

  .auth-logon {
    margin-top: 24px;
    display: flex;
  }

  .auth-logout {
    z-index: -1;
    transition: top .15s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 680px;
    left: 10px;
  }

  .auth-logout.is-hidden {
    top: 0;
  }
}

@media screen and (min-width: 768px) {
  .block-auth {
    display: block;
    position: relative;
  }

  .auth-logon {
    border: 1.5px solid var(--border-color);
    display: flex;
  }

  .auth-logout {
    width: 184px;
    z-index: -1;
    transition: top .25s cubic-bezier(.4, 0, .2, 1);
